import { title } from 'process';
import React, {useState} from 'react'
import TextField from '@mui/material/TextField';
import left from'../../tools/img/left.png';
import right from'../../tools/img/right.png';
import no from'../../tools/img/no.png';
import { Radio } from 'antd';
import { Input, Tooltip } from 'antd';
import { InfoCircleOutlined, UserOutlined } from '@ant-design/icons';
import 'antd/dist/antd.css';

function SectionMake({title, desc, setTitle, setDesc, titleColor, backgroundColor, onFileChange, attachment, secNum, setTemplate, setBackgroundColor, setTitleColor, setTitleSize, titleSize, setSectionHeight, sectionHeight}) {
    var numOfThisSection = parseInt(secNum);
    const [num, setNum] = useState(1);

    return (
        <div className="section-one-make">
            <div style={{fontSize:'20px', color:'#6B63F7', fontFamily:'Noto Sans KR', fontWeight:'700', display:'flex', justifyContent:'space-between', width:'100%'}}>
                <span>
                    Section {numOfThisSection} | 개요 - 내용
                </span>
                <span style={{display:'flex', justifyContent:'end', paddingRight:'5px'}}>
                    <Radio.Group defaultValue="1">
                        <Radio.Button value="1" onClick={e => setNum(1)}>
                            스타일
                        </Radio.Button>
                        <Radio.Button value="2" onClick={e => setNum(2)}>
                            내용
                        </Radio.Button>
                    </Radio.Group>
                </span>
            </div>
            { num === 2 ? 
            <>
            <div style={{display:'flex', justifyContent:'start', flexDirection:'column', width:'90%'}}>
                <div style={{display:'flex', justifyContent:'start', flexDirection:'column', textAlign:'left', marginTop:'10px'}}>
                    <span style={{fontSize:'16px'}}>■ 타이틀</span>                
                </div>
                <div style={{display:'flex', justifyContent:'start', flexDirection:'column', marginTop:'10px', width:'90%'}}>
                    <TextField label="Title" multiline rows={3} value={title} onChange={e => setTitle(e.currentTarget.value)} className="section-one-title-input"/>
                    <div style={{display:'flex', justifyContent:'start', flexDirection:'column'}}>
                        <div style={{display:'flex', justifyContent:'start'}}>
                            <span>- 타이틀 크기</span>                        
                            <input type="number" value={titleSize} onChange={e => setTitleSize(e.currentTarget.value)}/>
                        </div>
                    </div>
                </div>
            </div>
            <div style={{display:'flex', justifyContent:'start', flexDirection:'column', width:'90%'}}>
                <div style={{display:'flex', justifyContent:'start', flexDirection:'column', textAlign:'left', marginTop:'10px'}}>
                    <span style={{fontSize:'16px'}}>■ 내용</span>                
                </div>
                <div style={{display:'flex', justifyContent:'start', flexDirection:'column', marginTop:'10px', width:'90%'}}>
                    <TextField label="Description" multiline rows={3} value={desc} onChange={e => { setDesc(e.currentTarget.value); }} className="section-one-title-input"/>
                </div>
            </div>
            </>
            :
            <>
            <div style={{display:'flex', justifyContent:'start', flexDirection:'column', width:'90%'}}>
                    <div style={{display:'flex', justifyContent:'start', flexDirection:'column', textAlign:'left', marginTop:'10px'}}>
                        <span style={{fontSize:'16px'}}>■ 템플릿 유형</span>                
                    </div>
                    <Radio.Group defaultValue="1" style={{display:'flex', flexDirection:'row', justifyContent:'space-between', paddingTop:'6px'}}>
                        <div>
                            <span style={{fontSize:'13px'}}>⓵ 글 좌측, 이미지 우측</span>
                            <Radio.Button value="1" className="templateSelection" onClick={e => setTemplate(1)} clicked>
                                <img src={left} width={112} height={80}/>
                            </Radio.Button>
                        </div>
                        <div>
                            <span style={{fontSize:'13px'}}>⓵ 글 좌측, 이미지 X</span>
                            <Radio.Button value="2" className="templateSelection" onClick={e => setTemplate(2)} clicked>
                                <img src={no} width={112} height={80}/>
                            </Radio.Button>
                        </div>
                        <div>
                            <span style={{fontSize:'13px'}}>⓵ 글 우측, 이미지 좌측</span>
                            <Radio.Button value="3" className="templateSelection" onClick={e => setTemplate(3)} clicked>
                                <img src={right} width={112} height={80}/>
                            </Radio.Button>
                        </div>
                    </Radio.Group>
                </div>
                <div style={{display:'flex', justifyContent:'start', flexDirection:'column', width:'90%'}}>
                    <div style={{display:'flex', justifyContent:'start', flexDirection:'column', textAlign:'left', marginTop:'10px'}}>
                        <span style={{fontSize:'16px'}}>■ 색상</span>
                        <div style={{display:'flex', justifyContent:'space-between', flexDirection:'row' }}>      
                            <span className="color-container">
                                <span>섹션 배경</span>
                                <input style={{marginLeft:'3px'}} type="color" value={backgroundColor} onChange={e => setBackgroundColor(e.currentTarget.value)}/>
                            </span>       
                            <span className="color-container">
                                <span>타이틀</span>
                                <input style={{marginLeft:'3px'}} type="color" value={titleColor} onChange={e => setTitleColor(e.currentTarget.value)}/>
                            </span>       
                            <span className="color-container">
                                <span>내용</span>
                                <input style={{marginLeft:'3px'}} type="color" value={setBackgroundColor} onChange={e => setBackgroundColor(e.currentTarget.value)}/>
                            </span>
                        </div>
                    </div>
                </div>
                <div style={{display:'flex', justifyContent:'start', flexDirection:'column', width:'90%'}}>
                    <div style={{display:'flex', justifyContent:'start', flexDirection:'column', textAlign:'left', marginTop:'10px'}}>
                        <span style={{fontSize:'16px'}}>■ 사진</span>                
                    </div>

                    <input type="file" accept="image/*"  className="file-input" onChange={ e => onFileChange(e,numOfThisSection) } />
                
                </div>
                <div style={{display:'flex', justifyContent:'start', flexDirection:'column', width:'90%'}}>
                    <div style={{display:'flex', justifyContent:'start', flexDirection:'column', textAlign:'left', marginTop:'10px'}}>
                        <span style={{fontSize:'16px'}}>■ 섹션 높이</span>                
                    </div>
                    <Input
                        placeholder="섹션 높이를 설정하세요."
                        prefix={<UserOutlined className="site-form-item-icon" />}
                        suffix={
                            <Tooltip title="한 섹션의 높이">
                             px
                            </Tooltip>
                        }
                        value={sectionHeight} onChange={e => setSectionHeight(e.currentTarget.value)}
                        />
                </div>
            </>
            }
        </div>
    )
}

export default SectionMake
