import React from 'react'

function Section({sectionHeight, title, desc, attachment, templateNum, font, backgroundColor, titleColor, titleSize, applyButtonUse, applyButtonText, applyButtonColor, targets}) {

    const scrollDown = () => {
        targets.current.scrollIntoView({behavior: 'smooth'})
    }

    switch(parseInt(templateNum)){
        case 1:
            return (
            <div className="main-page-section" style={{backgroundColor:`${backgroundColor}`, height:`${sectionHeight}px`}}>
                <div className="main-section-right">       
                        {attachment && 
                        <div>
                            <img src={attachment} className="col-image"/>
                        </div>
                        }
                </div>
                <div className="main-section-left">
                    <div className="middle-section">
                        <span className="one-section-big" style={{fontFamily:`${font}`, color:`${titleColor}`, fontSize:`${titleSize}px`}}>
                            <pre style={{fontFamily:`${font}`}}>
                                {title}
                            </pre>
                        </span>
                        <span className="one-section-small">
                            <pre>
                                {desc}
                            </pre>
                        </span>
                        {applyButtonUse && 
                        <span className="apply-button" style={{backgroundColor:`${applyButtonColor}`}} onClick={e => scrollDown}>
                            {applyButtonText}
                        </span>
                        }
                    </div>  
                </div>
            </div>
            )
        case 2:
            return (
                <div className="main-page-section" style={{backgroundColor:`${backgroundColor}`, color:'#505050'}}>
                    <div className="main-section-left" style={{width:'80%'}}>
                        <div className="middle-section">
                            <span className="one-section-big" style={{fontFamily:`${font}`, textAlign:'center', color:`${titleColor}`, fontSize:`${titleSize}px`}}>
                                <pre>
                                    {title}
                                </pre>
                            </span>
                            <span className="one-section-small" style={{textAlign:'center'}}>
                                <pre>
                                    {desc}
                                </pre>
                            </span>
                        </div>  
                    </div>
                </div>
            )
        case 3:
            return (
                <div className="main-page-section" style={{backgroundColor:`${backgroundColor}`, color:'#505050'}}>
                    <div className="main-section-left">
                        <span className="one-section-big" style={{fontFamily:`${font}`, color:`${titleColor}`, fontSize:`${titleSize}px`}}>
                            <pre>{title}</pre>
                        </span>
                        <span className="one-section-small">
                            <pre>{desc}</pre>
                        </span>
                    </div>
                    <div className="main-section-right">               
                            {attachment && 
                            <div>
                                <img src={attachment} className="col-image"/>
                            </div>
                            }
                    </div>
                </div>
            )
    }
}

export default Section
