import React, {useEffect, useState, useRef} from 'react'
import './utils/MakePage.css'
import { dbService } from '../tools/fbase';
import {Link} from 'react-router-dom';
import Section from './utils/Section'
import SectionMake from './utils/SectionMake'
import LastSection from './utils/LastSection'
import LastSectionMake from './utils/LastSectionMake'
import MainSection from './utils/MainSection'
import './utils/MakeLanding.css'

import section0 from '../tools/img/section0.png';
import section1 from '../tools/img/section1.png';
import section3 from '../tools/img/section3.png';

const MakePage = ({history}) => {
    const targets = useRef(null)
    const [device, setDevice] = useState(false)
    // 섹션 1
    const [s1title, setS1title] = useState(" 랜딩페이지 제작부터\n데이터 분석까지 한번에");
    const [s1desc, setS1desc] = useState("스타트업 아이템 검증부터 신규 서비스, 이벤트까지 최고의 고객 전환율을 경험해보세요.");
    const [s1titleColor, setS1titleColor] = useState("rgba(0,0,0,1)");
    const [s1titleSize, setS1titleSize] = useState(30);
    const [attachment1, setAttachment1] = useState(`${section0}`);
    const [s1template, setS1template] = useState(1);
    const [s1backgroundColor, setS1backgroundColor] = useState("#6B63F7")
    const [sectionHeight1, setSectionHeight1] = useState(300)
    const [s1applyButton, setS1applyButton] = useState("9,900원에 시작하기");
    const [s1applyButtonUse, setS1applyButtonUse] = useState(false);
    const [s1applyButtonColor, setS1applyButtonColor] = useState("rgba(255,255,255,1)");
    // 섹션 2
    const [s2title, setS2title] = useState("코딩 필요없이 One-stop 랜딩페이지 제작 툴");
    const [s2titleColor, setS2titleColor] = useState("rgba(0,0,0,1)");
    const [s2titleSize, setS2titleSize] = useState(30);
    const [s2desc, setS2desc] = useState("더 이상 개발자 눈치 보지 말고, 간편하게 랜딩 페이지를 만들어보세요.");
    const [attachment2, setAttachment2] = useState(`${section1}`);
    const [s2template, setS2template] = useState(3);
    const [s2backgroundColor, setS2backgroundColor] = useState("rgba(255,255,255,1)")
    const [sectionHeight2, setSectionHeight2] = useState(300)

    const [s3title, setS3title] = useState("최적의 전환율을 위한 데이터 분석 자료를 실시간으로 확인하기");
    const [s3desc, setS3desc] = useState("조회수, 사용자 수, 전환율(CVR), 유입 채널 등 고객의 반응을 확인할 수 있는 유의미한 데이터를 개발자에게 요청할 필요없이 직접 확인해 보세요.");
    // 섹션3
    const [attachment3, setAttachment3] = useState(`${section3}`)


    // 라스트 신청 부분
    const [lasttitle, setLasttitle] = useState("One-stop 랜딩페이지 제작 툴, 가장 먼저 이용해 보세요");
    const [lastdesc, setLastdesc] = useState("사전신청하신 분들에겐 오픈 시 사용 가능한 일주일 무료 이용권이 제공됩니다.");
    const [lasttitleColor, setLasttitleColor] = useState("rgba(0,0,0,1)");
    const [lasttitleSize, setLasttitleSize] = useState(30);
    const [lasttemplate, setLasttemplate] = useState(1);
    const [lastbackgroundColor, setLastbackgroundColor] = useState("#6B63F7")
    const [lastapplyButton, setLastapplyButton] = useState("사전신청하고 혜택받기");
    const [lastApplies, setLastApplies] = useState(1);
    const [answers, setAnswers] = useState([])

    // 네비게이션
    const [naviTitle, setNaviTitle] = useState("Surfee")
    const [naviColor, setNaviColor] = useState("rgba(255,255,255,1)")
    const [naviTitleColor, setNaviTitleColor] = useState("#6B63F7")
    const [naviButtonTitle, setNaviButtonTitle] = useState("사전신청 하기")
    const [naviButtonColor, setNaviButtonColor] = useState("#6B63F7")
    const [naviButtonTitleColor, setNaviButtonTitleColor] = useState("black")

    // 사진들 아래는 기타
    const [mainColor, setMainColor] = useState("#6B63F7");
    const [urlId, setUrlId] = useState("");
    const [secNum, setSecNum] = useState(0);
    const [secNums, setSecNums] = useState([0,1,2,3,4,5,6]);
    const [font, setFont] = useState("Noto Sans KR");
    const [loading, setLoading] = useState(false);
    // CTA
    const [ctaUse, setCtaUse] = useState(false);
    const [ctaTitle, setCtaTitle] = useState("CTA 버튼");
    const [ctaColor, setCtaColor] = useState("rgba(0,0,0,1)");
    const [ctaLink, setCtaLink] = useState("www.naver.com");

    const onSubmit = async () => {
        var result = window.confirm("진짜 완성했죠?");
        if(result){
        setLoading(true);

        // 사진을 먼저 업로드하고 그 URL을 받아서 데이터로 넣어줘야한다.
        // const attachmentRef = stService.ref().child(`${userObj.uid}/${uuidv4()}`)
        // const response = await attachmentRef.putString(attachment, "data_url");
        // const attachmentURL = await response.ref.getDownloadURL();

        setLoading(false);
        history.push('/submit');
        history.go();
        }else{
            return
        }
    }

    const onFileChange =  (e, num) => {
        console.log("e", num);
        const {target:{files},} = e;
        const oneFile = files[0];
        const reader = new FileReader();
        reader.onloadend = (finishedEvent) => { // 로딩이 끝날 때 실행한다는 뜻.
            const {currentTarget:{result}} = finishedEvent;
            switch (num){
                case 1:
                    setAttachment1(result);
                    break;
                case 2:
                    setAttachment2(result);
                    break;
                case 3:
                    setAttachment3(result);
                    break;

            }
        }
        reader.readAsDataURL(oneFile);
    }

    const addSection = () => {
        if(secNums.length > 8){
            alert("제작가능한 최대 섹션 갯수에 한계가 있습니다!");
        }else{
            setSecNums([...secNums, secNums.length]);
        }
    }

    const deleteSection = () => {
        const secNumsTemp = secNums;
        secNumsTemp.pop();
        setSecNums(secNumsTemp);
    }

    const addInput = () => {
        setLastApplies(lastApplies + 1);
        console.log(lastApplies + 1);
    }

    const selectorList = secNums.map((item,index) => {
        if(item === secNum){
            var borderBottom = '';
        }else{
            var borderBottom = '1px solid black';
        }
        if(item === 0){
            return (
                <span index={index} style={{borderBottom:`${borderBottom}`}} className="make-page-section-selector" onClick={(e) => {
                    setSecNum(item);
                    }}>
                    기본세팅
                </span>
            )
        }else if(item === 1){
            return(
                <span index={index} style={{borderBottom:`${borderBottom}`}} className="make-page-section-selector" onClick={(e) => setSecNum(item)}>
                    <span style={{width:'70%'}}>네비</span>
                </span>
            )
        }else if(item === 2){
            return(
                <span index={index} style={{borderBottom:`${borderBottom}`}} className="make-page-section-selector" onClick={(e) => setSecNum(item)}>
                    <span style={{width:'70%'}}>푸터</span>
                </span>
            )
        }else if(item === 3){
            return(
                <span index={index} style={{borderBottom:`${borderBottom}`}} className="make-page-section-selector" onClick={(e) => setSecNum(item)}>
                    <span style={{width:'70%'}}>신청</span>
                </span>
            )
        }else if(item + 1 === secNums.length){
            return(
                <span index={index} className="section-add-button" onClick={e => addSection()}>
                    <span style={{width:'70%'}}>+</span>
                </span>
            )
        }else{
            return(
                <span index={index} style={{borderBottom:`${borderBottom}`}} className="make-page-section-selector" onClick={(e) => setSecNum(item)}>
                    <span style={{width:'70%'}}>SEC {item-3}</span>
                    {item+2 === secNums.length && <span className="sec-delete-button" onClick={(e) => deleteSection()}>x</span>}
                </span>
                )
        }
    })

    const selectorTable = () => {
        switch (secNum){
            case 0: // 기본 세팅
                return <>
                <MainSection
                    ctaUse={ctaUse} ctaLink={ctaLink} ctaTitle={ctaTitle} ctaUse={ctaUse} urlId={urlId}
                    setFont={setFont} setCtaUse={setCtaUse} setCtaLink={setCtaLink}
                    setCtaColor={setCtaColor} setCtaTitle={setCtaTitle} setUrlId={setUrlId}
                />
                </>
            case 1:
                return <>
                <div className="section-one-make">
                    <div>
                        네비게이션
                    </div>
                    <div>
                        <input type="text" value={naviTitle} onChange={e => setNaviTitle(e.currentTarget.value)}/>
                        <input type="color" value={naviTitleColor} onChange={e => setNaviTitleColor(e.currentTarget.value)}/>
                    </div>
                    <div>
                        <input type="text" value={naviButtonTitle} onChange={e => setNaviButtonTitle(e.currentTarget.value)}/>
                        <input type="color" value={naviButtonColor} onChange={e => setNaviButtonColor(e.currentTarget.value)}/>
                    </div>
                    <div>
                        네비게이션
                    </div>
                </div>
                </>
            case 2:
                return <>
                <div className="section-one-make">
                    푸터
                </div>
                </>
            case 3:
                return <>
                <LastSectionMake 
                    title={lasttitle} desc={lastdesc}  lastApplies={lastApplies} answers={answers}
                    setTitleColor={setLasttitleColor} setTitleSize={setLasttitleSize}
                    setTitle={setLasttitle} setDesc={setLastdesc} 
                    setBackgroundColor={setLastbackgroundColor} setAnswers={setAnswers}
                    addInput={addInput}
                />
                </>
            case 4:
                return <>
                <SectionMake 
                    title={s1title} desc={s1desc}  secNum="1" sectionHeight={sectionHeight1} 
                    attachment={attachment1} titleColor={s1titleColor} backgroundColor={s1backgroundColor}
                    setTitleColor={setS1titleColor} setTitleSize={setS1titleSize}
                    setTitle={setS1title} setDesc={setS1desc} 
                    onFileChange={onFileChange} setTemplate={setS1template}
                    setBackgroundColor={setS1backgroundColor} setSectionHeight={setSectionHeight1}
                    />
                    1번에 버튼 두기 말기
                    <input type="checkbox" value={s1applyButtonUse} onChange={e => setS1applyButtonUse(!s1applyButtonUse)} />
                    {s1applyButtonUse && <>
                        1번에 버튼 텍스트
                        <input value={s1applyButton} type="text" onChange={e => setS1applyButton(e.currentTarget.value)} className="section-one-title-input"/>
                        <input value={s1applyButtonColor} type="color" onChange={e => setS1applyButtonColor(e.currentTarget.value)} className="section-one-title-input"/>
                    </>
                    }
                </>
            case 5:
                const n = 2
                return <>
                    <SectionMake 
                    title={s2title} desc={s2desc}  sectionHeight={sectionHeight2}
                    setTitleColor={setS2titleColor} setTitleSize={setS2titleSize}
                    setTitle={setS2title} setDesc={setS2desc} 
                    onFileChange={onFileChange} secNum="2"
                    setTemplate={setS2template}
                    setBackgroundColor={setS2backgroundColor} setSectionHeight={setSectionHeight2}
                    />
                </>
            case 6:
                return <>
                <div className="section-one-make">
                    3번 타이틀
                    <input value={s3title} type="text" onChange={e => setS3title(e.currentTarget.value)} className="section-one-title-input"/>
                    3번 설명
                    <input value={s3desc} type="text" onChange={e => setS3desc(e.currentTarget.value)} className="section-one-title-input"/>
                    3번 사진
                    <input type="file" accept="image/*" className="file-input" onChange={e => onFileChange(e,3)}/>
                </div>
                </>
        }
    }

    return (
        <div className="make-page-container">
        {/* 여기서부터 메인페이지 베껴옴 */}
        <div className="main-page-container" style={{width:'70%'}}>  
            <div className="make-navigation">
                <span className="nav-bar-title" onClick={() => {history.push('/#/'); history.go();}} style={{cursor:'pointer', color:`${naviTitleColor}`}}>
                    {naviTitle}
                </span>
                <span className="nav-on">
                    <button className="nav-button" style={{backgroundColor:`${naviButtonColor}`, cursor:'pointer'}} onClick={() => window.scrollTo(0,document.body.scrollHeight)}>{naviButtonTitle}</button>
                </span>
            </div>     
            { secNums.length > 5 && 
            <Section 
                title={s1title} desc={s1desc} attachment={attachment1} 
                templateNum={s1template} font={font} backgroundColor={s1backgroundColor}
                titleColor={s1titleColor} titleSize={s1titleSize} 
                applyButtonUse={s1applyButtonUse} applyButtonText={s1applyButton} applyButtonColor={s1applyButtonColor}
                targets={targets} sectionHeight={sectionHeight1}
            />
            }
            { secNums.length > 6 && 
            <Section 
                title={s2title} desc={s2desc} attachment={attachment2} 
                templateNum={s2template} font={font} backgroundColor={s2backgroundColor}
                titleColor={s2titleColor} titleSize={s2titleSize}
                applyButtonUse={false} sectionHeight={sectionHeight2}
            />
            }
            { secNums.length > 7 && 
            <Section title={s3title} desc={s3desc} attachment={attachment3} templateNum="1" font={font} />
            }
            <LastSection 
                targets={targets}
                title={lasttitle} desc={lastdesc}
                font={font} backgroundColor={lastbackgroundColor}
                titleColor={lasttitleColor} titleSize={lasttitleSize}
                applyButtonText={lastapplyButton}
                answers={answers}
            />
            {ctaUse && 
            <div className="make-cta-button" style={{backgroundColor:`${ctaColor}`}}>{ctaTitle}</div>
            }
            <div className="make-footer">
                Footer Bar
            </div>     
        </div>
        {/* 여기까지 메인페이지 베껴옴 */}
            <div className="make-page-make-space">
                <div className="section-selector-container">
                    {selectorList}
                </div>
                <div className="section-table-container" style={{width:'100%'}}>
                    {selectorTable()}
                </div>
                <div style={{display: 'flex', justifyContent: 'center', marginTop:'10%'}}>
                    <button onClick={onSubmit} style={{width:'50%', position:'absolute', bottom:'100px'}}>등록</button>
                    <input type="checkbox" onChange={e => setDevice(!device)} value={device}/>
                </div>
            </div>
            {/* 로딩창 */}
        </div>
    )
}

export default MakePage
